.nav {
  width: 250px;
  display: flex;
  justify-content: space-evenly;
  margin-right: 16%;
}
.nav a {
  margin-left: 25px;
  color: #e6e6e6;
  font-size: 20px;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  text-decoration: none;
}

@media (max-width: 425px) {
  .nav {
    margin-right: 0%;

  }
  .nav a {
    margin-left: 0%;
  }
}
